window.addEventListener("DOMContentLoaded", (event) => {
    "use strict";

    $(window).on('load', function () {
        var pre_loader = $('#preloader')
        pre_loader.fadeOut(250, function () {
            $(this).remove();
        });
    });
    if (document.querySelector('.glide')) {
        let glide = new Glide('.glide', {
            type: 'carousel',
            startAt: 0,
            perView: 1,
            peek: {before: 0, after: 0},
            gap: 0,
            autoplay: 10000,
            animationDuration: 1500,
            hoverpause: !1,
            breakpoints: {
                992: {
                    peek: {before: 30, after: 30},
                },
            }
        })

        if (document.querySelectorAll('.glide__slide').length === 1) {
            glide.pause()
            glide.disable()
            glide.mount()
        } else {
            glide.play()
            glide.enable()
            glide.mount()
        }
    }

    AOS.init({
        offset: -100
    });

    var clipboardIpServer = new ClipboardJS('#server-ip-copy')
    clipboardIpServer.on('success', function (event) {
        let serverCopyIp = $(event.trigger)
        serverCopyIp.tooltip({
            trigger: 'click',
            placement: 'bottom'
        });
        setTooltip(serverCopyIp, 'CopiÃ© !');
        hideTooltip(serverCopyIp);

    });

    clipboardIpServer.on('error', function (e) {
        setTooltip('Erreur!');
        hideTooltip();

    });
});

function setTooltip(event, message) {
    event.tooltip('hide')
        .attr('data-original-title', message)
        .tooltip('show');
}

function hideTooltip(event) {
    setTimeout(function () {
        event.tooltip('hide');
    }, 1000);
}
